*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  height: 100vh;
  width: 100%;
}

.carousel-container {
  height: 500px;
  width: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.top {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  height: 50px;
  padding: 0 10px;
}

.arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  width: 50px;
  position: absolute;
  cursor: pointer;
  color: white;
  background-color: black;
  opacity: 50%;
  transition: opacity 300ms ease-in-out;
}

.arrow:hover {
  opacity: 70%;
}

.left-arrow {
  left: 0;
}

.right-arrow {
  right: 0;
}

.slides {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.slide {
  display: none;
  height: 100%;
  width: 100%;
}

.active-slide {
  display: flex;
  align-items: center;
  justify-content: center;
}

img {
  height: 100%;
}

.circle {
  height: 20px;
  width: 20px;
  border-radius: 100%;
  border: 1px solid black;
  cursor: pointer;
  transition: background-color 300ms ease-in-out;
}

.circle:hover {
  background-color: grey;
}

.active-circle {
  background-color: darkgray;
}
